:root {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smooth: never;
  --ff-body: "Mulish", sans-serif;
  --ff-heading: "Jost", sans-serif;
  --fs-xxl: 4.6rem;
  --fs-xl: 3.2rem;
  --fs-l: 2.4rem;
  --fs-20: 2rem;
  --fs-19: 1.9rem;
  --fs-18: 1.8rem;
  --fs-17: 1.7rem;
  --fs-16: 1.6rem;
  --fs-15: 1.5rem;
  --fs-14: 1.4rem;
  --fs-13: 1.3rem;
  --fs-12: 1.2rem;
  --fs-11: 1.1rem;
  --fs-10: 1rem;
  --fw-regular: 400;
  --fw-medium: 500;
  --fw-semi-bold: 600;
  --fw-bold: 700;
  --primary-1: #10002b;
  --primary-2: #240046;
  --primary-3: #3c096c;
  --primary-4: #5a189a;
  --primary-5: #7121c2;
  --primary-6: #7b2cbf;
  --secondary: #d96f04;
  --tertiary: #70bf2b;
  --gradient-default: linear-gradient(90deg, #7000ff -4.07%, #ff9b05 129.29%);
  --gradient-transparent: linear-gradient(90deg, #7000ff80 -4.07%, #ff9b0580 129.29%);
  --gradient-hero-text: linear-gradient(278deg, #3c096c 22.63%, #621ca3 95.05%);
  --gradient-hero-shape-bg: linear-gradient(180deg, #9574dd66 0%, #9574dd00 100%);
  --bg-hero: linear-gradient(180deg, #fff 0%, #efe9f6 100%);
  --bg-body: linear-gradient(180deg, #efe9f6 0%, #fff 100%);
  --bg-footer: linear-gradient(180deg, #fff -12.35%, #efe9f6 100%);
  --body: #10002b;
  --body-90: #10002be6;
  --body-80: #10002bcc;
  --white: #fff;
  --white-90: #ffffffe6;
  --white-80: #fffc;
  --white-70: #ffffffb3;
  --white-60: #fff9;
  --white-50: #ffffff80;
  --white-40: #fff6;
  --white-30: #ffffff4d;
  --white-20: #fff3;
  --white-10: #ffffff1a;
  --search-text: #979dac;
  --search-text-light: #bbb5c5;
  --section-border: #10002b1a;
  --light-bg: #fcf8ff;
  --light-border: #e4c8fc;
  --light-border2: #d4baea;
  --deactivated: #6f6d73;
  --shadow-color: 260deg 21% 51%;
  --shadow-elevation-low: .3px .5px .7px hsl(var(--shadow-color) / .17), .4px .7px .9px -1.2px hsl(var(--shadow-color) / .17), .8px 1.5px 1.9px -2.5px hsl(var(--shadow-color) / .17);
  --shadow-elevation-medium: .3px .5px .7px hsl(var(--shadow-color) / .18), .7px 1.3px 1.7px -.8px hsl(var(--shadow-color) / .18), 1.6px 3.2px 4px -1.7px hsl(var(--shadow-color) / .18), 3.8px 7.5px 9.5px -2.5px hsl(var(--shadow-color) / .18);
  --shadow-elevation-high: .3px .5px .7px hsl(var(--shadow-color) / .16), .8px 1.7px 2.1px -.4px hsl(var(--shadow-color) / .16), 1.5px 2.9px 3.7px -.7px hsl(var(--shadow-color) / .16), 2.3px 4.7px 5.9px -1.1px hsl(var(--shadow-color) / .16), 3.7px 7.3px 9.2px -1.4px hsl(var(--shadow-color) / .16), 5.7px 11.3px 14.2px -1.8px hsl(var(--shadow-color) / .16), 8.5px 17.1px 21.5px -2.1px hsl(var(--shadow-color) / .16), 12.5px 25px 31.4px -2.5px hsl(var(--shadow-color) / .16);
  --shadow-card-low: 1px 0px 0px 0px #ffffff4d inset, .8px .8px 2.2px #170e4101, 2px 2px 5.3px #170e4101, 3.8px 3.8px 10px #170e4101, 6.7px 6.7px 17.9px #170e4102, 12.5px 12.5px 33.4px #170e4102, 30px 30px 80px #170e4103;
  --shadow-card-medium: 1px 0px 0px 0px #ffffff4d inset, .8px .8px 2.2px #170e4102, 2px 2px 5.3px #170e4102, 3.8px 3.8px 10px #170e4103, 6.7px 6.7px 17.9px #170e4103, 12.5px 12.5px 33.4px #170e4104, 30px 30px 80px #170e4105;
  font-size: 62.5%;
}

html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  margin: .67em 0;
  font-size: 2em;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace;
  font-size: 1em;
}

a {
  background-color: #0000;
}

abbr[title] {
  border-bottom: none;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

img {
  border-style: none;
  max-width: 100%;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type="button"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type="reset"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring {
  outline: 1px dotted ButtonText;
}

[type="button"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

[type="reset"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: .35em .75em .625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  white-space: normal;
  max-width: 100%;
  padding: 0;
  display: table;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button {
  height: auto;
}

[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template, [hidden] {
  display: none;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  display: inline-block;
}

* {
  -webkit-tap-highlight-color: transparent !important;
  -webkit-focus-ring-color: transparent !important;
  outline: none !important;
}

html {
  scroll-behavior: smooth;
}

body {
  font-size: 1.6rem;
  font-family: var(--ff-heading);
  font-weight: var(--fw-regular);
}

h1, h2, h3, h4, h5, h6, p {
  position: relative;
}

.highlight {
  font-weight: var(--fw-bold);
  z-index: 0;
  display: inline-block;
  position: relative;
}

.highlight:before {
  content: "";
  font-weight: var(--fw-bold);
  box-sizing: border-box;
  background: url("highlight_generic.c6ea36ed.svg") right bottom 4px no-repeat;
  background-size: initial;
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

input {
  color: var(--primary-3);
  font-family: var(--ff-body);
  font-size: var(--fs-16);
  font-weight: var(--fw-semi-bold);
  appearance: none;
  letter-spacing: .14rem;
  background: none;
  border: none;
  border-radius: 0;
  outline: none;
  width: 100%;
  padding: 1.3rem 0;
}

input::placeholder {
  color: var(--search-text);
  font-family: var(--ff-heading);
  font-size: 1.2rem;
  font-weight: var(--fw-semi-bold);
  letter-spacing: .24rem;
  text-transform: uppercase;
  line-height: normal;
}

h1 {
  font-size: clamp(3.22rem, 2.8234rem + 1.0575vw, 4.6rem);
  font-style: normal;
  font-weight: var(--fw-bold);
  letter-spacing: .086956em;
  text-transform: uppercase;
  background: var(--gradient-hero-text);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  margin: 0;
  line-height: 180%;
}

h1.small {
  font-size: clamp(2.24rem, 1.9641rem + .7356vw, 3.2rem);
  font-weight: var(--fw-regular);
  letter-spacing: .075em;
}

h2 {
  color: var(--primary-3, #3c096c);
  letter-spacing: .166em;
  text-transform: uppercase;
  font-size: clamp(2rem, 1.8851rem + .3065vw, 2.4rem);
  font-style: normal;
  line-height: 165%;
}

.label {
  color: var(--primary-1, #10002b);
  letter-spacing: .24rem;
  text-transform: uppercase;
  margin-bottom: 1.6rem;
  font-size: 1.2rem;
  font-style: normal;
  line-height: 130%;
}

@media (width <= 720px) {
  .label {
    margin-bottom: 1.2rem;
  }
}

p {
  color: var(--body-80, #10002bcc);
  font-family: var(--ff-body);
  font-size: var(--fs-16);
  font-weight: var(--fw-regular);
  line-height: 165%;
}

.unbuttonize {
  text-align: inherit;
  font: inherit;
  appearance: none;
  background-color: #0000;
  border: none;
  border-radius: 0;
  margin: 0;
  padding: 0;
}

.bold {
  font-weight: var(--fw-bold);
}

.displayNone {
  display: none !important;
}

.link {
  color: var(--primary-3, #3c096c);
  font-size: var(--fs-14);
  font-weight: var(--fw-medium);
  letter-spacing: .3rem;
  text-transform: uppercase;
  border-bottom: 1px dashed #10002b66;
  padding: .8rem 0 .8rem 4.4rem;
  transition: all .2s;
  position: relative;
}

.link:before {
  content: "";
  background: url("right_arrow.d786c1be.svg") 0 no-repeat border-box;
  width: 100%;
  height: 100%;
  transition: all .2s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(1px);
}

.link:hover {
  color: var(--primary-1);
}

.link:hover:before {
  transform: translateX(9px);
}

@media (width <= 1220px) {
  .link {
    margin-top: 2.4rem;
  }
}

@media (width <= 720px) {
  .link {
    font-size: var(--fs-12);
    padding-left: 3.6rem;
  }

  .link:before {
    transform: translateX(-18px)scale(.8);
  }

  .link:hover:before {
    transform: translateX(-10px)scale(.8);
  }
}

.button {
  font-size: var(--fs-12);
  font-weight: var(--fw-medium);
  letter-spacing: .1rem;
  text-transform: uppercase;
  border-radius: 1rem;
  align-self: stretch;
  align-items: center;
  gap: .8rem;
  padding: .8rem 1.55rem;
  line-height: 133.2%;
  transition: all .2s;
  display: flex;
  position: relative;
}

.button img {
  width: 2rem;
  height: 2rem;
}

.button:hover {
  background: #fff !important;
}

.fancyBorder:after {
  content: "";
  border: 1px dashed var(--light-border2);
  border-radius: 1rem;
  transition: all .25s;
  display: block;
  position: absolute;
  inset: 0;
}

.fancyBorder:hover:after {
  border: 1px dashed var(--secondary);
  inset: -5px;
}

.viewMoreButton {
  color: var(--primary-3);
  font-size: var(--fs-12);
  font-weight: var(--fw-medium);
  letter-spacing: .3rem;
  text-transform: uppercase;
  cursor: pointer;
  border: 1px dashed #d4baea;
  border-radius: 1rem;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 3rem;
  padding: 1rem 2rem 1rem 4rem;
  transition: all .2s;
  display: inline-block;
  position: relative;
}

.viewMoreButton:before {
  content: "";
  background: url("down_arrow.dc2dfe71.svg") 0 0 no-repeat border-box;
  width: 32px;
  height: 32px;
  transition: all .2s;
  position: absolute;
  top: 4px;
  left: 5px;
  transform: scale(.85);
}

.viewMoreButton:hover:before {
  transform: rotate(-90deg)scale(.85);
}

@media (width <= 720px) {
  .viewMoreButton {
    margin-top: 1.6rem;
  }
}

.chip {
  background: var(--light-bg);
  border: 1px solid var(--light-border);
  box-shadow: var(--shadow-card-low);
  border-radius: 3rem;
  flex-flow: row;
  align-items: center;
  gap: .6rem;
  padding: .6rem .8rem .6rem 1rem;
  display: inline-flex;
}

.chip:hover svg.close {
  fill: var(--primary-6);
}

.chip svg {
  fill: var(--primary-2);
  width: 1.6rem;
  height: 1.6rem;
}

.chip svg.close {
  fill: var(--light-border2);
  width: 1.8rem;
  height: 1.8rem;
  margin-left: .2rem;
}

.chip span {
  color: var(--primary-2);
  text-align: center;
  font-size: var(--fs-14);
  font-weight: var(--fw-regular);
  letter-spacing: .04rem;
  text-wrap: nowrap;
  line-height: 2rem;
}

.search {
  background: linear-gradient(#fcfafd, #fcfafd) padding-box, var(--gradient-transparent) border-box;
  border: 1px solid #0000;
  border-radius: 10rem;
  align-self: stretch;
  align-items: center;
  gap: 1.6rem;
  padding: 0 3.2rem;
  display: flex;
}

.search:focus-within {
  background: linear-gradient(#fff, #fff) padding-box, var(--gradient-transparent) border-box;
}

.search svg {
  fill: var(--secondary);
  width: 24px;
  height: 24px;
}

.productCard {
  border: 1px solid var(--white-50);
  box-sizing: border-box;
  background: linear-gradient(105deg, var(--white-40) 16.65%, var(--white-10) 79.09%);
  box-shadow: var(--shadow-card-medium);
  border-bottom-color: #eae4f0;
  border-right-color: #eae4f0;
  border-radius: 2rem;
  flex-direction: column;
  flex-shrink: 0;
  align-items: flex-start;
  display: flex;
}

.productCard header {
  flex-direction: column;
  align-self: stretch;
  padding: 2rem;
  display: flex;
}

.productCard header .photoBlock {
  box-shadow: var(--shadow-card-medium);
  background: radial-gradient(57.14% 57.14%, #fff 71.58%, #fff0 100%);
  border-radius: 2rem;
  justify-content: center;
  align-self: stretch;
  align-items: center;
  height: 25.2rem;
  display: flex;
  position: relative;
}

.productCard header .photoBlock:after {
  content: attr(brand);
  border: 1px dashed var(--light-border);
  background: var(--light-bg);
  color: var(--primary-3);
  font-size: var(--fs-10);
  font-weight: var(--fw-regular);
  text-transform: uppercase;
  letter-spacing: .1rem;
  opacity: .5;
  border-right-width: 0;
  border-radius: 1rem 0 0 1rem;
  justify-content: center;
  align-items: center;
  padding: .5rem 1.3rem;
  line-height: 133.2%;
  display: flex;
  position: absolute;
  top: 2.8rem;
  right: 0;
}

.productCard header img {
  border-radius: 2rem;
  flex-shrink: 0;
  width: 15.4rem;
  height: 20rem;
}

.productCard footer {
  flex-direction: column;
  justify-content: flex-end;
  align-self: stretch;
  align-items: center;
  gap: 2.4rem;
  height: 15.2rem;
  padding: .4rem 2.6rem 2rem;
  display: flex;
}

.productCard footer .detailsBlock {
  flex-direction: column;
  justify-content: center;
  align-self: stretch;
  align-items: flex-start;
  gap: 1.4rem;
  display: flex;
}

.productCard footer .detailsBlock h5 {
  color: var(--primary-1);
  font-family: var(--ff-body);
  font-size: var(--fs-13);
  font-weight: var(--fw-bold);
  letter-spacing: .14rem;
  text-align: left;
  text-transform: uppercase;
  line-height: 133.2%;
}

.productCard footer .priceBlock {
  align-items: center;
  gap: 1rem;
  display: flex;
}

.productCard footer .priceBlock span {
  color: var(--primary-1);
  font-family: var(--ff-body);
  font-size: var(--fs-15);
  font-weight: var(--fw-bold);
  letter-spacing: .14rem;
  line-height: 133.2%;
}

.productCard footer .priceBlock span.original {
  color: var(--deactivated);
  font-weight: var(--fw-regular);
  text-decoration: line-through;
}

.productCard footer .buttonsBlock {
  justify-content: flex-start;
  align-items: center;
  gap: 1.6rem;
  width: 100%;
  display: flex;
}

.productCard footer .buttonsBlock a.play {
  background: var(--white-40);
}

.productCard footer .buttonsBlock a.play span {
  background: var(--gradient-default);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

@media (width <= 1700px) {
  .productCard header {
    padding: 1.7rem;
  }

  .productCard header .photoBlock {
    height: 22.5rem;
  }

  .productCard header img {
    width: auto;
    height: 17.5rem;
  }

  .productCard footer {
    gap: 2.2rem;
    padding: .4rem 2rem 2rem;
  }

  .productCard footer .detailsBlock {
    gap: 1.2rem;
  }

  .productCard footer .buttonsBlock {
    gap: 1.4rem;
  }

  .productCard footer .buttonsBlock a.button {
    gap: 1rem;
    padding: .9rem 1.25rem;
  }
}

@media (width <= 720px) {
  .productCard {
    width: 26rem;
  }

  .productCard header {
    padding: 1.4rem;
  }

  .productCard header .photoBlock {
    height: 20rem;
  }

  .productCard header .photoBlock:after {
    padding: .5rem 1.2rem;
    font-size: 9px;
  }

  .productCard header img {
    width: auto;
    height: 15rem;
  }

  .productCard footer {
    height: initial;
    gap: 1.8rem;
    padding: .4rem 2rem 2rem;
  }

  .productCard footer .detailsBlock {
    gap: 1.2rem;
  }

  .productCard footer .detailsBlock h5 {
    font-size: var(--fs-12);
  }

  .productCard footer .priceBlock span {
    font-size: var(--fs-14);
  }

  .productCard footer .buttonsBlock {
    gap: 1.2rem;
    width: 100%;
  }

  .productCard footer .buttonsBlock a {
    flex: 1;
    justify-content: center;
    gap: 1.2rem;
  }

  .productCard footer .buttonsBlock a.play span {
    white-space: nowrap;
    width: 35px;
  }
}

#header {
  justify-content: space-between;
  align-items: center;
  padding: 4.8rem min(6%, 10rem);
  display: flex;
  position: relative;
}

#header #logo {
  z-index: 10;
  width: 16rem;
  transition: transform .5s;
  position: relative;
}

#header nav {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
  display: flex;
}

#header nav a {
  color: var(--primary-1);
  font-size: var(--fs-13);
  font-weight: var(--fw-semi-bold);
  letter-spacing: .24rem;
  text-transform: uppercase;
  border: 1px dashed #0000;
  border-radius: 2.4rem;
  padding: .5rem 2.4rem;
  transition: all .1s;
}

#header nav a:hover, #header nav a:focus {
  color: var(--primary-5);
  background-color: var(--light-bg);
  border-color: var(--light-border);
}

#header nav svg {
  fill: var(--primary-1);
  width: 3rem;
  height: 3rem;
  margin-left: 2.4rem;
}

#header nav svg:hover, #header nav svg:focus {
  fill: var(--primary-5);
}

#header #mobileMenu {
  display: none;
}

#header #mobileMenu .icon {
  cursor: pointer;
  background: none;
  width: 24px;
  height: 24px;
  padding: 1.6rem;
  display: block;
  position: relative;
}

#header #mobileMenu .icon .menu, #header #mobileMenu .icon .menu:before, #header #mobileMenu .icon .menu:after {
  content: "";
  background: var(--primary-6);
  width: 20px;
  height: 2px;
  transition: background .3s, top .3s .3s, transform .3s;
  display: block;
  position: absolute;
}

#header #mobileMenu .icon .menu {
  top: 15px;
  left: 6px;
}

#header #mobileMenu .icon .menu:before {
  top: -6px;
}

