  padding: 0 4rem;
  display: flex;
}

@media (width <= 1220px) {
  #featuredProducts {
    padding-left: 0;
    padding-right: 0;
  }

  #featuredProducts .wrapper {
    gap: 4rem;
    margin: 4.8rem 0;
  }
}

@media (width <= 720px) {
  #featuredProducts {
    padding: 12rem 0 8rem;
  }

  #featuredProducts .titleBlock {
    margin-bottom: 3rem;
  }

  #featuredProducts .wrapper {
    flex-flow: row;
    justify-content: flex-start;
    gap: 3rem;
    margin: 0;
    padding: 0 4.8rem 4rem;
    overflow: scroll;
  }

  #featuredProducts .link {
    margin-top: 1rem !important;
  }
}

#productsPage {
  background: var(--bg-body);
}

#productsPage #header {
  background: var(--bg-hero);
}

#productsContainer {
  flex-flow: row;
  gap: 10rem;
  margin: 2rem 4% 0;
  display: flex;
  position: relative;
}

#productsContainer .wrapper {
  width: 100%;
}

@media (width <= 1000px) {
  #productsContainer {
    gap: 0;
  }

  #productsContainer .filters {
    z-index: 10;
    position: absolute;
    transform: translateX(-1000px);
  }

  #productsContainer .filters #productFilters {
    -webkit-backdrop-filter: blur(80px);
    backdrop-filter: blur(80px);
    background: #ffffff4d;
    box-shadow: 2.8px 2.8px 2.2px #00000005, 6.7px 6.7px 5.3px #00000007, 12.5px 12.5px 10px #00000009, 22.3px 22.3px 17.9px #0000000b, 41.8px 41.8px 33.4px #0000000d, 100px 100px 80px #00000012;
  }
}

@media (width <= 720px) {
  #productsContainer {
    margin: 2rem 0;
  }

  #productsContainer .wrapper .appliedFilters {
    gap: 4rem;
    width: 100%;
    padding: 2rem 0 2rem 6%;
    overflow: auto visible;
  }

  #productsContainer .wrapper .appliedFilters .filterButton {
    display: block;
  }

  #productsContainer .wrapper #allProducts {
    flex-flow: row;
    justify-content: flex-start;
    padding-left: 6%;
    display: flex;
    overflow-x: auto;
  }
}

#productsContainer .filters {
  flex-flow: column;
  gap: 0;
  display: flex;
}

#productsContainer .filters .searchBlock {
  background: linear-gradient(#fcfafd, #fcfafd) padding-box, var(--gradient-transparent) border-box;
  border: 1px solid #0000;
  flex-flow: row;
  align-items: center;
  gap: 1rem;
  width: 100%;
  margin-bottom: 4rem;
  padding: 0 2rem;
  display: flex;
}

#productsContainer .filters .searchBlock:focus-within {
  background: linear-gradient(white, white) padding-box, var(--gradient-transparent) border-box;
}

#productsContainer .filters .searchBlock input {
  padding: 1.2rem 0;
  font-size: var(--fs-12) !important;
}

#productsContainer .filters .searchBlock input::placeholder {
  color: #9e96ab;
  letter-spacing: .14rem;
  font-size: var(--fs-11) !important;
}

#productsContainer .filters .searchBlock svg {
  fill: var(--secondary);
  width: 2.4rem;
  height: 2.4rem;
}

#productFilters {
  border: 1px solid var(--white-50);
  box-shadow: var(--shadow-card-medium);
  box-sizing: border-box;
  background: linear-gradient(105deg, var(--white-40) 16.65%, var(--white-10) 79.09%);
  border-bottom-color: #eae4f0;
  border-right-color: #eae4f0;
  border-radius: 1rem;
  min-width: 23rem;
  height: -moz-fit-content;
  height: fit-content;
  position: sticky;
  top: 4rem;
}

#productFilters header {
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

#productFilters header p {
  font-family: var(--ff-heading);
  font-size: var(--fs-11);
  font-weight: var(--fw-semi-bold);
  color: var(--light-border2);
  text-transform: uppercase;
  letter-spacing: .3rem;
}

#productFilters header svg {
  fill: var(--light-border2);
  width: 20px;
  height: 20px;
}

#productFilters .searchBlock {
  background: linear-gradient(#fcfafd, #fcfafd) padding-box, var(--gradient-transparent) border-box;
  border: 1px solid #0000;
  flex-flow: row;
  align-items: center;
  gap: 1rem;
  padding: 0 2rem;
  display: flex;
}

#productFilters .searchBlock:focus-within {
  background: linear-gradient(white, white) padding-box, var(--gradient-transparent) border-box;
}

#productFilters .searchBlock input {
  padding: 1.2rem 0;
  font-size: var(--fs-12) !important;
}

#productFilters .searchBlock input::placeholder {
  color: #bbb5c5;
  letter-spacing: .14rem;
  font-size: var(--fs-11) !important;
}

#productFilters .searchBlock svg {
  fill: var(--secondary);
  width: 2.4rem;
  height: 2.4rem;
}

.appliedFilters {
  align-items: center;
  gap: 2.4rem;
  display: flex;
}

.appliedFilters .filterButton {
  background: var(--light-bg);
  box-shadow: var(--shadow-elevation-medium);
  border: 1px solid #fff;
  border-radius: 1rem;
  max-height: 42px;
  padding: 10px;
  display: none;
}

.appliedFilters .filterButton svg {
  fill: var(--primary-3);
  width: 2rem;
  height: 2rem;
}

.appliedFilters > div {
  align-items: center;
  gap: 1.2rem;
  display: flex;
}

.appliedFilters a {
  color: var(--primary-2);
  font-size: var(--fs-13);
  font-weight: var(--fw-regular);
  letter-spacing: .04rem;
}

.appliedFilters a:hover {
  color: var(--primary-5);
}

#allProducts {
  grid-template-columns: repeat(auto-fill, minmax(28rem, 1fr));
  gap: 3rem;
  padding: 4rem 0;
  display: grid;
}

#category {
  flex-flow: column;
  padding: 1.5rem 0;
  display: flex;
}

#category a {
  color: var(--primary-2);
  padding: .9rem 2rem .9rem 5rem;
  position: relative;
}

#category a span {
  transition: all .25s;
  display: inline-flex;
}

#category a:hover {
  background: #7b2cbf05;
}

#category a:hover span {
  transform: translateX(1rem);
}

#category a.active {
  color: var(--primary-6);
  background-color: #7b2cbf06;
}

#category a.active span {
  transform: translateX(0) !important;
}

#category a.active:after {
  content: "";
  background-color: var(--primary-6);
  width: 2px;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

#category a:before {
  content: "";
  background: url("category.66d14df4.svg") center / contain no-repeat border-box;
  width: 18px;
  height: 100%;
  position: absolute;
  top: 0;
  left: 2rem;
  overflow: hidden;
}

#category a.active:before {
  background: url("check.a3c06504.svg") center / contain no-repeat border-box;
  overflow: hidden;
}

